.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .nav-var {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 3;
  }

  .deck {
    width: 100%;
  }

  .contents {
    width: 60%;
    margin-top: 80px;
    margin-bottom: 300px;
  }

  .building-image-container {
    position: relative;
    width: 100%;
    img {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
  }

  .bottom {
    width: 100%;
  }
}
