.contents {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;

  .header {
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    height: 50px;
    margin-bottom: 70px;
    position: relative;

    .molding-container {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
    }
    .column {
      margin-bottom: 5px;
      .header-title {
        font-size: 28px;
        color: black;
        z-index: 3;
        font-weight: 500;
        margin-left: 20px;
        margin-right: 3px;
      }

      .header-title-eng {
        z-index: 3;
        margin-left: 5px;
      }
    }
    .title-eng {
      padding-bottom: 4px;
    }
    .title-kor {
      margin-left: 7px;
    }
  }

  .body {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    .item-container {
      margin-bottom: 120px;
    }
